.universal-preloader {
  border: 10px solid #ccc;
  border-radius: 50%;
  border-top: 10px solid #ff6008;
  width: 70px;
  height: 70px;
  animation: spin 2s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}
