@import "../../scss/resources/resources";

.breadcrumbs {
  &-section {
    display: none;
    background-color: $grey-bg;
    padding: 17px 0 15px;

    @include min-width($landscape-tablet) {
      display: block;
    }
  }

  &-list {
    display: flex;

    li {
      &::after {
        content: '•';
        font-size: 13px;
        margin: 0 10px;
      }

      &:last-child::after {
        content: none;
      }
    }
  }

  &-link {
    font-size: 14px;
    line-height: 40px;
    color: $black;
    text-transform: uppercase;

    &:active,
    &:hover {
      color: #ff6008;
    }
  }
}